import React, { ReactNode, CSSProperties } from "react";
import { Button as VDSButton } from "@vds/buttons";

interface Props {
  ariaLabel?: string;
  children: ReactNode;
  className?: string;
  buttonText?: string;
  disabled?: boolean;
  display?: "flex" | "inline-block" | "block";
  href?: string;
  inverted?: boolean;
  onClick?: Function;
  primary?: boolean;
  secondary?: boolean;
  size?: "small" | "large";
  width?: number | string;
  "data-analyticstrack"?: string;
  "data-clickstream"?: string;
  "data-sitecat-cta"?: string;
  "data-sitecat-datatrack"?: string;
  "data-sitecat-level"?: string;
  "data-sitecat-position"?: string;
  "data-track"?: string;
  style?: CSSProperties;
  role?: string;
}

export function Button(props: Props) {
  return <VDSButton {...props}>{props.buttonText || props.children}</VDSButton>;
}

