import React from "react";

import {
  Button,
  // FlexContainer,
  BottomTextContainer,
  HeadlineContainer,
  // StyledButton,
  // HeadlineContainer,
  IMG
} from "./ShopTile.style";
import { Title, Micro, Body } from "../Typography";

type ShopTileProps = any;
const feedbackCall = (arg, sdfsfd) => null;

interface Props {
  tileContent: ShopTileProps;
  isMobile: boolean;
}

export function ShopTile({
  tileContent: {
    backgroundImageURLWeb,
    backgroundImageURLAndroid,
    backgroundImageURLiOS,
    backgroundColor,
    borderColor,
    eyebrowCopy,
    eyebrowCopyColor,
    headlineCopy,
    headlineCopyColor,
    legalCopy,
    legalCopyColor,
    primaryCTAType,
    primaryCTAColor,
    primaryCTALabelCopy,
    primaryCTAWebDestination,
    pegaCards,
    retailPrice,
    adjustedPrice,
    dataTrack
  },
  isMobile
}: Props) {
  // const checkBackgroundImageUrl = () => {
  //   if (backgroundImageDesktopURL || backgroundImageMobileURL) {
  //     if (isMobile) {
  //       return `url(${backgroundImageMobileURL})`;
  //     }
  //     return `url(${backgroundImageDesktopURL})`;
  //   }
  //   return null;
  // };

  return (
    <Button
      onClick={() => feedbackCall(pegaCards, primaryCTAWebDestination)}
      data-track={dataTrack}
      style={{
        border: `1px solid ${borderColor ? borderColor : "#d8dada"}`
      }}
    >
      <IMG src={backgroundImageURLWeb} alt="" />
      {/* <FlexContainer> */}
      {eyebrowCopy && (
        <HeadlineContainer>
          {isMobile ? (
            <Micro primitive="span" color={eyebrowCopyColor} bold>
              {eyebrowCopy}
            </Micro>
          ) : (
            <Body color={eyebrowCopyColor} size="large" bold>
              {eyebrowCopy}
            </Body>
          )}
        </HeadlineContainer>
      )}

      {/* {buttonText && (
          <StyledButton primitive="span" size="small" bold>
            {buttonText}
          </StyledButton>
        )} */}

      {/* {backgroundImageURL && <IMG src={backgroundImageURL} alt="" />} */}

      <BottomTextContainer>
        {headlineCopy && (
          <Title
            primitive="p"
            size={isMobile ? "small" : "medium"}
            color={headlineCopyColor}
            style={{ marginBottom: legalCopy || adjustedPrice || retailPrice ? "4px" : "0" }}
          >
            {headlineCopy}
          </Title>
        )}

        {legalCopy && (
          <Micro primitive="span" color={legalCopyColor}>
            {legalCopy}
          </Micro>
        )}

        {adjustedPrice && (
          <Body primitive="span" size={isMobile ? "small" : "large"} style={{ display: "inline-block" }}>
            {adjustedPrice}
          </Body>
        )}

        {retailPrice && (
          <Body
            primitive="span"
            size={isMobile ? "small" : "large"}
            color="#333333"
            style={{ textDecoration: "line-through", display: "inline-block", marginLeft: "4px" }}
          >
            {retailPrice}
          </Body>
        )}
      </BottomTextContainer>
      {/* </FlexContainer> */}
    </Button>
  );
}
