import React, { CSSProperties, ReactNode } from "react";

import { Title as VDSTitle } from "@vds/typography";
import { Wrapper } from "./Wrapper";

interface Props {
  children: ReactNode;
  style?: CSSProperties;
  primitive?: "h1" | "h2" | "h3" | "h4" | "h5" | "h6" | "span" | "p";
  size?: "XSmall" | "small" | "medium" | "large" | "XLarge" | "2XLarge";
  color?: ColorList | string;
  id?: string;
  tabIndex?: number;
  className?: string;
}

type ColorList =
  | "#000000" /* black */
  | "#ffffff" /* white */
  | "#d8dada" /* light gray */
  | "#747676" /* dark gray */
  | "#d52b1e";

export function Title({ primitive, size, color, id, tabIndex, children, style, className }: Props) {
  return (
    <Wrapper primitive={primitive} className={className} style={{ display: "block", ...style }}>
      <VDSTitle primitive={primitive} size={size} color={color} id={id} tabIndex={tabIndex}>
        {children}
      </VDSTitle>
    </Wrapper>
  );
}
