import styled, { css } from "styled-components";
import { media } from "../../utils/style";
const styles = css`
  /* border: 1px solid #d8dada; */
  /* display: flex; */
  /* flex-direction: column; */
  text-decoration: none;
  height: 334px;
  position: relative;

  ${media.mobile`
    height: 209px;
  `}
`;

export const Button = styled.button`
  ${styles};
  padding: 0;
  border-radius: 0;
  background: 0;
  text-align: left;
  width: 100%;
  cursor: pointer;

  ${media.mobile`
    /* padding: 16px; */

  `}
`;

export const Div = styled.div`
  ${styles};
`;

const buttonStackStyles = css`
  flex-direction: column;

  button:first-of-type {
    margin-bottom: 10px;
  }
`;

export const BottomContentContiner = styled.div`
  position: absolute;
  bottom: 24px;
  left: 24px;

  ${media.mobile`
    bottom: 16px;
    left: 16px;
  `}
`;

export const ButtonContainer = styled.div`
  position: absolute;
  bottom: 24px;
  left: 24px;
  right: 24px;
  
  display: flex;
  justify-content: space-between;

  .iHaveADeviceButton span span {
    border-bottom: 1px solid #000000;
  }

  .iHaveADeviceButton {
    text-decoration: none;
    background: 0;
    border: 0;
    padding: 0;
    cursor: pointer;
  }

  ${media.smalldesktop`${buttonStackStyles}`}

  ${media.smallscreen`${buttonStackStyles}`}

  ${media.mobile`
    position: static;

    .iHaveADeviceButton {
      position: absolute;
      bottom: 16px;
      left: 16px;
    }

    .shopNowButton {
      position: absolute;
      top: 50%;  
      left: 50%;
      transform: translate(-50%, -50%);
    }
  `}
`;

export const PaddingContainer = styled.div`
  padding: 24px;
  height: 100%;

  ${media.mobile`
    padding: 16px;
 `}
`;

export const FlexContainer = styled.div`
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
`;

export const TopContainer = styled.div`
  position: absolute;
  top: 24px;
  left: 24px;
  right: 24px;

  ${media.mobile`
    top: 16px;
    left: 16px;
    right: 16px;
  `}
`;

export const IMG = styled.img`
  max-width: 146px;
  max-height: 138px;
  display: block;
  margin: 24px auto;

  ${media.mobile`
    max-height: 73px;
    margin: 12px auto;
  `}
`;
