import { css } from "styled-components";

export const mobile_min = 320;
export const mobile_max = 767;
export const tablet_min = 768;
export const tablet_max = 1019;
export const smalldesktop_min = 1020;
export const smalldesktop_max = 1271;
export const desktop_min = 1272;

export const media = {
  mobile: (...args) => css`
    @media only screen and (max-width: ${mobile_max + 0.5}px) {
      ${css(...args)};
    }
  `,
  tablet: (...args) => css`
    @media only screen and (min-width: ${tablet_min}px) and (max-width: ${tablet_max + 0.5}px) {
      ${css(...args)};
    }
  `,
  only_tablet: (...args) => css`
    @media only screen and (min-width: ${tablet_min}px) and (max-width: ${tablet_max + 0.5}px) {
      ${css(...args)};
    }
  `,
  smallscreen: (...args) => css`
    @media only screen and (max-width: ${tablet_max + 0.5}px) {
      ${css(...args)};
    }
  `,
  smalldesktop: (...args) => css`
    @media only screen and (min-width: ${smalldesktop_min}px) and (max-width: ${smalldesktop_max + 0.5}px) {
      ${css(...args)};
    }
  `,
  desktop: (...args) => css`
    @media only screen and (min-width: ${desktop_min}px) {
      ${css(...args)};
    }
  `,
  bigscreen: (...args) => css`
    @media only screen and (min-width: ${smalldesktop_min}px) {
      ${css(...args)};
    }
  `,
  nomobile: (...args) => css`
    @media only screen and (min-width: ${tablet_min}px) {
      ${css(...args)};
    }
  `
};

export const isMobile =
  Math.max(document.documentElement.clientWidth, window.innerWidth || 0) < mobile_max + 1;
export const isTablet =
  Math.max(document.documentElement.clientWidth, window.innerWidth || 0) > mobile_max - 1 &&
  Math.max(document.documentElement.clientWidth, window.innerWidth || 0) < tablet_max + 1;
export const isDesktop =
  Math.max(document.documentElement.clientWidth, window.innerWidth || 0) > smalldesktop_min - 1;
export const isSmallMobile =
  Math.max(document.documentElement.clientWidth, window.innerWidth || 0) < mobile_min + 1;
export const isSmallDesktop =
  Math.max(document.documentElement.clientWidth, window.innerWidth || 0) < smalldesktop_min + 5;
