import React, { createRef, useRef } from "react";
import { Micro } from "@vds/typography";

import { Button } from "../Buttons";
import { Body } from "../Typography";

import { Div, FlexContainer, ButtonContainer, PaddingContainer } from "./common.styles";

const feedbackCall = (arg, sdfsfd) => null;

type DeviceTileProps = any;

const formatMdn = mdn => mdn;

export const AddDeviceTile = ({
  eyebrowCopy,
  eyebrowCopyColor,
  backgroundColor,
  borderColor,
  primaryCTAType,
  primaryCTALabelCopy,
  primaryCTAColor,
  secondaryCTALabel,
  secondaryCTAColor,
  primaryCTAWebDestination,
  secondaryCTAWebDestination,
  backgroundImageURLWeb,
  pegaCards,
  viewport,
  dataTrack
}: DeviceTileProps) => {
  return (
    <Div
      style={{
        background: viewport === "mobile" ? backgroundColor : `url(${backgroundImageURLWeb})`,
        backgroundPosition: "center",
        backgroundColor: backgroundColor,
        border: `1px solid ${borderColor ? borderColor : "#d8dada"}`
      }}
    >
      <PaddingContainer>
        <FlexContainer className="container">
          {viewport === "mobile" ? <p>{eyebrowCopy}</p> : <p>{eyebrowCopy}</p>}

          {/* {backgroundImageURLWeb && <img src={backgroundImageURLWeb} alt="" />} */}
          {/* <MockDeviceContainer>
            <div style={{ textAlign: "center", position: "relative" }}>
              <VerticalCross />
              <HorizontalCross />
            </div>
          </MockDeviceContainer> */}
          <ButtonContainer>
            <Button
              onClick={() => feedbackCall(pegaCards, primaryCTAWebDestination)}
              data-track={JSON.stringify({
                type: "link",
                name: `${dataTrack}|${primaryCTALabelCopy}`,
                format: "pega_card"
              })}
              size="small"
              className="shopNowButton"
              ariaLabel={`${primaryCTALabelCopy} to ${eyebrowCopy}`}
              role="link"
            >
              {primaryCTALabelCopy}
            </Button>

            <button
              onClick={() => feedbackCall(pegaCards, secondaryCTAWebDestination)}
              data-track={JSON.stringify({
                type: "link",
                name: `${dataTrack}|${secondaryCTALabel}`,
                format: "pega_card"
              })}
              role="link"
              aria-label={`${secondaryCTALabel} to ${eyebrowCopy}`}
              className="iHaveADeviceButton"
            >
              <Body
                primitive="span"
                size={viewport === "desktop" ? "large" : "small"}
                style={{ display: "inline-block" }}
              >
                {secondaryCTALabel}
              </Body>
            </button>
          </ButtonContainer>
        </FlexContainer>
      </PaddingContainer>
    </Div>
  );
};
