import React, { CSSProperties, ReactNode } from "react";

import { Body as VDSBody } from "@vds/typography";
import { Wrapper } from "./Wrapper";

interface Props {
  children: ReactNode;
  style?: CSSProperties;
  color?:
    | (string)
    | "#000000" /* black */
    | "#ffffff" /* white */
    | "#d8dada" /* light gray */
    | "#747676" /* dark gray */
    | "#d52b1e" /* red */
    | "#333333" /* very dark gray */
    | "#0088ce" /* teal blue */
    | "#0077B4" /* dark teal */;
  ariaLabelledBy?: string;
  bold?: boolean;
  id?: string;
  primitive?: "h1" | "h2" | "h3" | "h4" | "h5" | "h6" | "span" | "p";
  size?: "small" | "medium" | "large";
  tabIndex?: number;
  className?: string;
}

export function Body({
  children,
  style,
  color,
  ariaLabelledBy,
  bold,
  id,
  primitive,
  size,
  tabIndex,
  className
}: Props) {
  return (
    <Wrapper primitive={primitive} className={className} style={{ display: "block", ...style }}>
      <VDSBody
        color={color}
        ariaLabelledBy={ariaLabelledBy}
        bold={bold}
        id={id}
        primitive={primitive}
        size={size}
        tabIndex={tabIndex}
      >
        {children}
      </VDSBody>
    </Wrapper>
  );
}
