import React, { CSSProperties } from "react";

import { Micro as VDSMicro } from "@vds/typography";

interface Props {
  children: Node | string;
  style?: CSSProperties;
  color?: "#000000" /* black */ | "#ffffff" /* white */ | string;
  ariaLabelledBy?: string;
  bold?: boolean;
  id?: string;
  primitive?: "h1" | "h2" | "h3" | "h4" | "h5" | "h6" | "span" | "p";
  tabIndex?: number;
}

export function Micro({ children, style, color, ariaLabelledBy, bold, id, primitive, tabIndex }: Props) {
  return (
    <span style={{ display: "block", ...style }}>
      <VDSMicro
        color={color}
        ariaLabelledBy={ariaLabelledBy}
        bold={bold}
        id={id}
        primitive={primitive}
        tabIndex={tabIndex}
      >
        {children}
      </VDSMicro>
    </span>
  );
}
