import React, { useEffect, useState } from "react";
import ReactCarousel from "react-slick";
import { withOrientationChange } from "react-device-detect";

import "./style.css";

let Carousel: any = props => {
  // const { isLandscape, isPortrait } = props;
  // const [slidesToShow, setSlidesToShow] = useState(2);
  const [currentSlide, setCurrentSlide] = useState(0);

  useEffect(() => {
    const el: any = document.querySelector(`#${props.id} .slick-track`);

    if (el && el.children) {
      [...el.children].forEach((child, index) => {
        const interactiveElements = child.querySelectorAll("button, a");

        if (interactiveElements && interactiveElements.length) {
          interactiveElements.forEach(element => {
            if (!child.classList.contains("slick-active") && !child.classList.contains("slick-current")) {
              element.setAttribute("tabindex", "-1");
              element.setAttribute("aria-hidden", "true");
            } else {
              element.setAttribute("tabindex", "0");
              element.setAttribute("aria-hidden", "false");
            }
          });
        }

        if (
          props.children[index] &&
          props.children[index].props &&
          props.children[index].props.clickable !== false
        ) {
          child.setAttribute("tabindex", child.getBoundingClientRect().x > 0 ? "0" : "-1");
          child.setAttribute("aria-label", child.innerText);
          child.setAttribute("role", "link");
        } else {
          child.removeAttribute("tabindex");
          child.removeAttribute("aria-hidden");
        }
        // if (
        //   child.childNodes &&
        //   child.childNodes[0] &&
        //   child.childNodes[0].nodeName &&
        //   child.childNodes[0].nodeName.toLowerCase() === "div"
        // ) {
        // child.childNodes[0].setAttribute("tabindex", "-1");
        // child.childNodes[0].setAttribute("aria-hidden", "true");
        // }
        // const button = child.querySelector("button");
        // if (button) {
        //   if (index !== currentSlide && index !== currentSlide + 1) {
        //     button.setAttribute("tabindex", "-1");
        //     button.setAttribute("aria-hidden", "true");
        //   } else {
        //     button.setAttribute("aria-hidden", "false");
        //     button.setAttribute("tabindex", "0");
        //   }
        // }
      });
    }
  }, [currentSlide]);

  const setFocus = i => {
    const el = document.querySelector(`#${props.id} .slick-track`);

    if (el && el.children && el.children[i]) {
      const childEl: any = el.children[i];

      if (childEl) {
        childEl.focus();
      }
    }
  };

  const settings = {
    dots: true,
    customPaging: function(i) {
      return (
        <button
          style={{ outline: 0 }}
          data-track={`slide item ${i + 1}`}
          aria-label={`slide item ${i + 1} ${currentSlide === i ? "selected" : ""}`}
        />
      );
    },
    afterChange: function(i) {
      setCurrentSlide(i);
      setFocus(i);
    },
    infinite: false,
    speed: 500,
    slidesToShow: 2,
    slidesToScroll: 1,
    accessibility: false,
    arrows: false,
    responsive: [
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 4
        }
      },
      {
        breakpoint: 696,
        settings: {
          slidesToShow: 3
        }
      },
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 2
        }
      }
    ]
  };

  // useEffect(() => {
  //   if (isLandscape) {
  //     if (window.innerWidth < 668) {
  //       setSlidesToShow(3);
  //     } else {
  //       setSlidesToShow(4);
  //     }
  //   }

  //   if (isPortrait) {
  //     if (window.innerWidth < 281) {
  //       setSlidesToShow(1);
  //     } else {
  //       setSlidesToShow(2);
  //     }
  //   }
  // }, [isLandscape, isPortrait]);

  return (
    <div id={props.id}>
      <ReactCarousel {...settings} {...props.settings}>
        {props.children}
      </ReactCarousel>
    </div>
  );
};

Carousel = withOrientationChange(Carousel);

export { Carousel };
