export const Button = styled.div`
  margin: 1px 0 0 0;
  padding: 0px;
  position: relative;
  text-align: left;
  background: 0;
  font-size: 0;
  width: 100%;
  cursor: pointer;
  padding-bottom: 100%;
  height: 0;

  /* margin: 1px auto;
  padding: 0px;
  position: relative;
  text-align: left;
  background: 0;
  font-size: 0;
  display: block;
  cursor: pointer;
  padding-bottom: 100%;
  height: 0;
  width: 100%; */

  ${media.mobile`
    padding: 0 8px;
    height: auto;
    /* width: auto; */

    :focus {
      z-index: 99999;
      outline-offset: -1px;
    }
  `}
`;

export const BottomTextContainer = styled.div`
  position: absolute;
  bottom: 24px;
  left: 24px;
  right: 24px;
  max-width: 85%;

  ${media.tablet`
    bottom: 20px;
    left: 20px;
    right: 20px;
  `}

  ${media.mobile`
    bottom: 16px;
    left: 16px;
    right: 16px;
  `}
`;

export const StyledButton = styled(Body)`
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  position: absolute;
  padding: 4px 14px;
  border-radius: 20px;
  border: 1px solid #000;

  ${media.mobile`
    position: static;
    padding: 4px 8px;
    display: block;
    margin: auto;
    transform: initial;
  `}
`;

export const HeadlineContainer = styled.span`
  position: absolute;
  top: 24px;
  left: 24px;
  right: 24px;

  ${media.mobile`
    top: 16px;
    left: 16px;
    right: 16px;
  `}
`;

export const IMG = styled.img`
  width: 100%;
  height: 100%;
  display: block;
  position: absolute;

  ${media.mobile`
    position: static;
  `}
`;
