import React from "react";
import { Micro } from "@vds/typography";

import { Body, Title } from "../Typography";
import { Button, IMG, TopContainer, BottomContentContiner } from "./common.styles";

const truncate = input => (input && input.length > 12 ? `${input.substring(0, 12)}...` : input);

const feedbackCall = (arg, sdfsfd) => null;

type DeviceTileProps = any;

const formatMdn = mdn => mdn;

const checkBackgroundColor = (backgroundColor: string) => {
  let color = [];

  if (backgroundColor) {
    color = backgroundColor.split("#");
  }

  if (color[1]) {
    return color[1];
  }
  return "";
};

export const DefaultDeviceTile = ({
  borderColor,
  backgroundColor,
  viewport,
  primaryCTAWebDestination,
  eyebrowCopy,
  eyebrowCopyColor,
  imageURL,
  headlineCopy,
  headlineCopyColor,
  subheadCopy,
  subheadCopyColor,
  primaryCTALabelCopy,
  pegaCards,
  accessibilityText,
  defaultTile,
  dataTrack
}: DeviceTileProps) => {
  const suspended = eyebrowCopy && eyebrowCopy.toLowerCase().includes("suspended");
  const checkViewPort = viewport => {
    if (viewport === "tablet") {
      return "XSmall";
    } else if (viewport === "mobile") {
      return "small";
    }
    return "medium";
  };
  return (
    <Button
      onClick={() => feedbackCall(pegaCards, primaryCTAWebDestination)}
      data-track={dataTrack}
      role="link"
      style={{
        border: `1px solid ${borderColor ? borderColor : "#d8dada"}`,
        backgroundColor: backgroundColor ? backgroundColor : "#ffffff"
      }}
    >
      <TopContainer>
        {viewport === "mobile" ? (
          <Micro primitive="span" color={eyebrowCopyColor} bold>
            {eyebrowCopy}
          </Micro>
        ) : (
          <Body
            primitive="span"
            color={eyebrowCopyColor}
            size={viewport === "tablet" ? "small" : "large"}
            bold
          >
            {eyebrowCopy}
          </Body>
        )}
        {imageURL && (
          <span style={{ display: "block", margin: "0 auto" }}>
            <IMG
              src={`${imageURL}?bgc=${checkBackgroundColor(backgroundColor)}`}
              style={{ opacity: suspended ? ".5" : "1" }}
              alt={accessibilityText}
            />
          </span>
        )}
      </TopContainer>
      <BottomContentContiner>
        <Title
          color={suspended ? "#747676" : headlineCopyColor}
          primitive="span"
          size={checkViewPort(viewport)}
        >
          {defaultTile ? headlineCopy : truncate(headlineCopy)}
        </Title>

        {!defaultTile && (
          <>
            <Body
              color={suspended ? "#747676" : subheadCopyColor}
              primitive="span"
              size={viewport === "desktop" ? "large" : "small"}
              style={{ marginTop: "4px" }}
            >
              {formatMdn(subheadCopy)}
            </Body>

            <span style={{ marginTop: "8px", display: "inline-block" }}>
              <Body
                primitive="span"
                size={viewport === "desktop" ? "large" : "small"}
                style={{ borderBottom: "1px solid #000" }}
              >
                {primaryCTALabelCopy}
              </Body>
            </span>
          </>
        )}
      </BottomContentContiner>
    </Button>
  );
};
